
body{
  margin:0px;
  padding:0px;
  font-family:Verdana, Geneva, Tahoma, sans-serif;
}
#topnav{
  padding:15px;
  margin:0px;
  background:black;

}
#topnav li{
  display:inline;
  color:white;
  padding:10px;
}
.mylink{
  color:white;
  text-decoration: none;

}
.mylink:hover{
  color:red;

}
.mylink:focus{
  color:yellow;
  text-decoration: underline;
}
.center{
  text-align:center;
}
.leftblock{
  width:30%;
  float:left;
}
.rightblock{
  width:65%;
  float:right;
  margin-left:4%;
}
.margin{
  margin-top:50px;
}
.space{
  margin-left:3%;
  width:30%;
  text-align:center;
  float:left;
}
.btn{
  margin-left:5px;
}
.userdetails{
  width:29.33;
  margin:2%;
  padding:1%;
  float:left;
  height:200px;
  text-align:center;
  box-shadow:0px 0px 2px blue;
  border-radius:0px 30px 0px 30px;
}
.userdetails img{
  width:100%;
  height:80%;

}
.city{
  width:25%;
  float:left;
}
.iteminfo{
  height:auto;
  width:21%;
  margin:1%;
  padding:1%;
  float:left;
  box-shadow:0px 0px 4px black;
}
.iteminfo:hover{
  box-shadow:0px 0px 4px blue;
  cursor:pointer;
  color:orange;

}
.par{
  height:100px;
  width:30%;
  margin-left:3%;
  border:5px solid black;
  box-sizing:border-box;
  float:left;
  text-align: center;
}
.userinfo{
  padding:10px 20px  10px;
  margin:15px;
  background: orange;
  text-decoration: none;
  color:green;

}
.userinfo:focus{
  
  background:skyblue;
  color:white;
  border-radius:10px;
  box-shadow: 0px 0px 15px skyblue;

}
table{
  
}
table ,tr, th, td{
  border:none;

}
tr,th,td{
  padding:7px;
  box-shadow:0px 0px 1px grey;
}
img{
  border-radius:50px;
}
i{
  color:red;
  font-size:12px;
  display:block;
}
.mylinks{
  cursor: pointer;
}

